    // 获取订单详情
    getOrderDetail() {
        apiAfterSaleDetail({ id: this.id }).then((res) => {
            this.orderData = res;
            this.refund_total_amount =
                res.goods_info.order_goods_sum.sum_refund_amount;
        });
    }

    // 同意售后
    afterSaleAgree() {
        apiAfterSaleAgree({ id: this.id }).then((res) => {
            this.getOrderDetail();
        });
    }

    // 拒绝售后
    afterSaleRefuse() {
        apiAfterSaleRefuse({ id: this.id }).then((res) => {
            this.getOrderDetail();
