    // 确认收货
    afterSaleConfirmGoods() {
        apiAfterSaleConfirmGoods({ id: this.id }).then((res) => {
            this.getOrderDetail();
        });
    }

    // 拒绝收货
    afterSaleRefuseGoods() {
        apiAfterSaleRefuseGoods({ id: this.id }).then((res) => {
            this.getOrderDetail();
        });
